import React, { Component } from 'react';
import doneimg from '../img_source/done.png';

class TextInfo extends Component {
    static defaultProps = {
        info: {
            task: '할 일',
            during: '결산 기간',
            index: 0
        }
    }

    state = {
        editing: false,
        isdone: false,
        task: '',
        during: ''
    }

    handleRemove = () => {
        const { info, onRemove } = this.props;
        onRemove(info.index);
    }

    handleToggleEdit = () => {
        const { editing } = this.state;
        this.setState({ editing: !editing });
        console.log("Toggle!");
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        });
    }

    handleDone = () => {
        const { isdone } = this.state;
        this.setState({ isdone: !isdone });
        console.log(isdone);
    }

    componentDidUpdate(prevProps, prevState) {
        const { info, onUpdate } = this.props;
        if(!prevState.editing && this.state.editing) {
            this.setState({
                task: info.task,
                during: info.during
            });
        }

        if(prevState.editing && !this.state.editing) {
            onUpdate(info.index, {
                task: this.state.task,
                during: this.state.during
            });
        }
    }

    render() {
        let style = {
            fontFamily: 'Nanum Gothic',
            color: 'white',
            border: '5px outset slategray',
            width: '450px',
            height: '80px',
            padding: '8px',
            margin: '8px'
        };

        const textstyle = {
            color: 'rgba(231,220,61,0.8)'
        }

        const donestyle = {
            fontFamily: 'Nanum Gothic',
            color: 'white',
            border: '5px outset slategray',
            padding: '8px',
            margin: '8px',
            backgroundColor: 'rgba(248, 69, 69, 0.904)'
        }

        const boxstyle = {
            fontFamily: 'Nanum Gothic',
            height: '22.5px',
            backgroundColor: 'slategray',
            color: 'white',
            fontSize: '16px'
        }

        const imgstyle = {
            display: 'none'
        }

        const doneimgstyle = {
            width: '90px',
            float: 'right',
            position: 'relative',
            top: '-61px'
        }

        const { editing } = this.state;

        if (editing) {
            return(
                <div style={ style }>
                    <div>
                        <input value={this.state.task} name="task" placeholder="할 일" onChange={this.handleChange} style={boxstyle} />
                    </div>
                    <div>
                        <input value={this.state.during} name="during" placeholder="수행 기간" onChange={this.handleChange} style={boxstyle} />
                    </div>
                    <button onClick={this.handleToggleEdit} className="btn">적용</button>
                    <button onClick={this.handleRemove} className="btn">삭제</button>
                </div>
            );
        }

        // 일반 모드
        const {
            task, during, index
        } = this.props.info;

        const { isdone } = this.state;

        return(
            <div style={ (isdone) ? donestyle : style }>
                <div><i>*{index + 1}. *</i></div>
                <div><b style={ textstyle }>{task}</b>를(을)</div>
                <div><b>{during}</b>일 안에 해낸다!</div>
                <button onClick={this.handleToggleEdit} className="btn btn-edit">수정</button>
                <button onClick={this.handleRemove} className="btn btn-del">삭제</button>
                <button onClick={this.handleDone} className="btn btn-done">완료</button>
                <img src={doneimg} style={ (isdone) ? doneimgstyle : imgstyle }></img>
            </div>
        );
    }
}

export default TextInfo;